/*
  请求路由MAP
  path 请求路径 method 方法 noAlert 不显示消息框 rest 是否rest资源
  rsaKey RSA加密的字段 setToken 需要设置token
 */
export default {
  //验证token
  'verifyToken': {
    noAlert: true,
    path: '/auth/token',
    method: 'GET',
  },
  //登录注册
  'register': {
    path: '/auth/register',
    method: 'POST',
    rsaKeys: ['password', 'password_confirm'],
    setToken: true,
  },
  'login': {
    path: '/auth/login',
    method: 'POST',
    rsaKeys: ['password'],
    setToken: true,
  },
  'pubKey': {
    path: '/auth/key',
    method: 'GET'
  },
  //分区列表获取 提交
  'postColumn': {
    path: '/api/rest/column',
    method: 'POST'
  },
  'getColumns': {
    path: '/api/rest/column',
    method: 'GET'
  },
  //文章提交 请求文章列表
  'postArticle': {
    path: '/api/rest/article',
    method: 'POST'
  },
  'getArticleById': {
    rest: true,
    path: '/api/rest/article/:id',
    method: 'GET'
  },
  'getArticles': {
    path: '/api/rest/article',
    method: 'GET'
  },
  //评论
  'postComment': {
    path: '/api/rest/comment',
    method: 'POST'
  },
  //搜索 点赞
  'search': {
    path: '/article/search',
    method: 'GET'
  },
  'artLikes': {
    path: '/article/likes/:id',
    method: 'POST',
    rest: true
  },
  //上传
  'upload': {
    path: '/upload',
    method: 'POST'
  },
  'uploadAvatar': {
    path: '/upload/user',
    method: 'POST'
  },
  //获取 更新用户信息
  'getUserInfo': {
    path: '/userinfo',
    method: 'GET'
  },
  'putUserInfo': {
    path: '/userinfo',
    method: 'PUT'
  },
}