/*
mixin混入
  - 语法糖 写入 data methods computed 等属性方法
  - 引入组件内 使用 mixins: [IMPORT_MIXIN] 会将此mixin的属性方法与组件进行拼合(assign)
  - 组件存在同名属性方法 以组件优先
*/
import { createNamespacedHelpers } from 'vuex'
const { mapActions, mapGetters, mapState } = createNamespacedHelpers('artLikes')

export default {
  //created 和 aid 发生改变的时候 都获取初始点赞状态
  created() {
    this.initLike = this.isLike(this.aid)
  },
  watch: {
    aid() {
      this.initLike = this.isLike(this.aid)
    },
  },
  computed: {
    ...mapGetters(['isLike']),
    ...mapState(['step_num']),
    aid() {
      return this.$route.params?.id
    },
  },
  methods: {
    artLikesHandler() {
      //当前点赞状态 true =>点击取消点赞 || false =>点击添加点赞
      let act = this.isLike(this.aid) ? 'pull' : 'push'
      //如果初始进入已经是点赞的 push动作不能+1 而是+0(vuex处理)
      this.changeLike({ aid: this.aid, action: act, initLike: this.initLike })
      //这里就直接向服务器发送点赞请求了
      this.sendLike({ aid: this.aid })

    },
    ...mapActions(['changeLike', 'sendLike'])
  }
}