import instance from '@/api/httpBase'
import API_LIST from '@/configs/api.config'
import store from 'store'
import encrypt from '@/utils/encrypt'

import HTTP_CONFIG from '@/configs/http.config'

/*
  因为http函数最后是作为插件挂载到了Vue 的prototype上 因此执行时 this指向的是当前调用的Vue示例
*/

export default async function http({ type, data }) {
  //非正确type
  if (!(type in API_LIST)) {
    throw new Error('API请求错误')
  }

  //读取配置
  let { path, method = 'get', rest = false, rsaKeys = false,
    setToken = false, noAlert = false } = API_LIST[type]
  method = method.toLowerCase()

  //POST请求如果存在id(REST资源) :id => :{id}
  if (rest) {
    let id = data.id
    path = path.replace(/:(id)/, `${id}`)
  }

  // 如果请求需要走RSA认证 (登录/注册),执行密码前端一次加密(后端还有二次加密)
  // 新增(包括密码确认验证,rsaKey变为数组形式)
  // 不能使用forEach(同步) 而是 for..of
  if (data && rsaKeys) {
    for (let key of rsaKeys) {
      data[key] = await encrypt(data[key])
    }
  }

  // 如果是GET请求将 data 转换成 params (比如 {params :{ id: 1234 }} => url => url/xxx/1234)
  data = (method === 'get') ? { params: data } : data

  //发送请求
  try {
    let result = await instance?.[method](path, data)

    //登录/注册行为 需要设置token+vuex
    if (setToken) {
      const { token } = result.data
      store.set(HTTP_CONFIG.TOKEN_NAME, token)
      //同时调用Vuex响应登录操作
      this.$store.dispatch('login')
    }

    // if (!noAlert) {
    //   let message = result.message
    //   this?.$notify.success({
    //     title: '成功',
    //     message,
    //     offset: 100
    //   })
    // }

    //返回promise(服务端响应结果)
    return result?.data;
  } catch (error) {
    console.error(error)
    if (error.response && !noAlert) {
      let message = error.response.data.message
      //弹窗
      this.$notify.error({
        title: '错误',
        message,
        offset: 100
      })
    }
    return Promise.reject(error)
  }

}

























