<template>
  <li class="blog-comment--item">
    <div class="blog-comment--user">
      <div class="blog-comment--avatar">
        <img :src="avatar" class="blog-comment--avatar" :alt="nickname" />
      </div>
      <div class="blog-comment--info">
        <span class="blog-comment--name">{{ nickname }}</span>
        <span class="blog-comment--floor">{{ floor }}楼</span>
      </div>
    </div>
    <p class="blog-comment-content" v-html="comment.content"></p>
    <p class="blog-comment-date">{{ comment.date }}</p>
  </li>
</template>

<script>
export default {
  name: 'VueBlogCommentItem',
  props: {
    floor: {
      type: Number
    },
    comment: {
      type: Object
    }
  },
  data() {
    return {}
  },

  computed: {
    nickname() {
      return this.comment.uid.nickname
    },
    avatar() {
      return this.comment.uid.avatar
    }
  },

  methods: {}
}
</script>

<style lang="stylus"></style>