<template>
  <div class="blog-article--info">
    <span class="blog-info--item">
      <i class="el-icon-date"></i> {{ info.date }}
    </span>
    <span class="blog-info--item">
      <i class="el-icon-user"></i> {{ info.nickname }}
    </span>
    <!-- <div class="visible-xs"> -->
    <span
      style="cursor: pointer"
      class="blog-info--item"
      @click="artLikesHandler"
      :class="{ active: isLike(aid) }"
    >
      <i class="el-icon-thumb"></i>
      {{ like_num }}
    </span>
    <span class="blog-info--item">
      <i class="el-icon-view"></i> {{ info.hit_num }}</span
    >
    <span class="blog-info--item">
      <i class="el-icon-s-comment"></i> {{ info.comment_num }}
    </span>
    <!-- </div> -->
  </div>
</template>

<script>
import MIXIN_ART_LIKES from '@/core/mixin_artLikes'
export default {
  name: 'VueBlogArticleInfoBar',
  mixins: [MIXIN_ART_LIKES],
  props: {
    info: {
      type: Object
    }
  },
  computed: {
    like_num() {
      return Math.max(this.info.like_num + this.step_num, 0)
    }
  },
  methods: {}
}
</script>

<style lang="stylus">
.blog-info--item
  user-select none

.blog-info--item.active
  color theme-color-orange
</style>