/**
 * @description 根据模板格式化传入的日期对象
 * @param {Object Date} date 对象
 * @param {String} format 日期格式化字符串
 * @return {String}  yyyy-MM-dd hh:mm:ss =>  "2011-01-01 08:00:00"
 */

/*
  str.padStart(targetLength [, padString])
  targetLength
  当前字符串需要填充到的目标长度。如果这个数值小于当前字符串的长度，则返回当前字符串本身。
  padString 可选
  填充字符串。如果字符串太长，使填充后的字符串长度超过了目标长度，则只保留最左侧的部分，其他部分会被截断。此参数的默认值为 " "（U+0020）。
*/
function formatDate(date = new Date(), format = 'yyyy/MM/dd hh:mm:ss') {
  const dateMap = {
    'y': String(date.getFullYear()),
    'M': String(date.getMonth() + 1),
    'd': String(date.getDate()),
    'h': String(date.getHours()),
    'm': String(date.getMinutes()),
    's': String(date.getSeconds()),


  };
  return Object.entries(dateMap).reduce((acc, [reg, value]) => {
    //根据传入的模板匹配字长padStart
    //举例 如果dateMap里获得 'M':7 => 参数传入'MM' => len = 2  '7'.padStart(2,'0') => 输出 07
    //如果dateMap里获得 'M':12 => 参数传入'M' => len = 1  '7'.padStart(1,'0') => 输出 12
    let len = acc.match(new RegExp(`${reg}+`, 'g'))[0].length
    return acc.replace(new RegExp(`${reg}+`, 'g'), value.padStart(len, '0'))
  }, format)

}
module.exports = formatDate