<template>
  <el-card class="blog-article--wrap">
    <h3 class="blog-article--title">{{ article.title }}</h3>
    <ArticleInfoBar :info="info" />
    <div class="blog-cover--wrap">
      <el-image
        v-if="article.cover"
        class="blog-article--cover"
        :src="article.cover"
        :alt="article.title"
        :title="article.title"
        fit="cover"
      ></el-image>
    </div>
    <!-- 使用v-html 前加上v-once保证安全性 -->
    <div
      class="blog-article--content"
      v-if="article.content"
      v-once
      v-html="article.content"
    />
  </el-card>
</template>

<script>
import ArticleInfoBar from '@/components/article/ArticleInfoBar.vue'
import { createNamespacedHelpers } from 'vuex'
const { mapActions } = createNamespacedHelpers('artLikes')

export default {
  name: 'VueBlogArticleContent',
  provide() {
    return {
      article: this.article
    }
  },
  props: {
    article: {
      type: Object,
      default() {
        return {
          title: '默认标题',
          author: { nickname: '佚名' }
        }
      }
    }
  },
  components: { ArticleInfoBar },
  computed: {
    nickname() {
      return this.article.author?.nickname
    },
    info() {
      const { date, like_num, hit_num, comment_num } = this.article
      return { date, like_num, hit_num, comment_num, nickname: this.nickname }
    }
  },

  mounted() {},

  methods: {
    ...mapActions(['changeStepNum'])
  },
  beforeDestroy() {
    //离开页面之前要清空点赞计数器
    this.changeStepNum(0)
  }
}
</script>

<style lang="stylus">
.blog-article--wrap
  align-items center
  .blog-article--title
    line-height 60px
    font-size 24px
    color theme-bgcolor-light
    background-color theme-color-orange
  .blog-cover--wrap
    width 100%
    display flex
    justify-content center
    margin-top: padding-per * 2
    padding: padding-per * 3 0
    border-radius: bd-radius-per * 3
    background-color rgba(210, 210, 210, 0.3)
  .blog-article--cover
    max-width 50%
  .blog-article--info
    padding: padding-per * 2 0
    border-radius bd-radius-per
    border 2px dashed #ccc
    justify-content space-around
    align-items center

.blog-article--content
  padding padding-per (padding-per * 2)
  text-align justify
</style>