<template>
  <el-card>
    <div class="blog-comment--edit">
      <p class="blog-edit--tip">看帖是喜欢，评论才是真爱</p>
      <!-- div自定义v-model  ? -->
      <div
        class="blog-edit--editor"
        ref="editor"
        @input="putValue"
        contenteditable
      ></div>
      <el-button type="primary" @click="submitReply">评论</el-button>
    </div>
  </el-card>
</template>

<script>
export default {
  name: 'VueBlogCommentEditor',
  inject: ['getArticleById'],
  props: {
    id: {
      type: String
    }
  },
  data() {
    return {
      content: ''
    }
  },
  //eventBus激活评论框
  mounted() {
    this.$bus.$on('focusCommentEditor', () => this.focusEditor())
  },

  methods: {
    //赋值
    putValue(e) {
      this.content = e.target.innerHTML
    },
    focusEditor() {
      //console.log(this.$refs?.editor,'------')
      this.$refs?.editor?.focus()
    },
    async submitReply() {
      if (this.content.trim().length === 0) {
        this.$notify.warning({
          message: '请填写评论内容'
        })
        this.focusEditor()
        return
      }
      try {
        await this.$http({
          type: 'postComment',
          data: { content: this.content, aid: this.id }
        })

        //inject 调用父组件的 getArticleById方法 提交完评论后刷新列表
        this.getArticleById(this.id)

        //清空内容
        this.$refs.editor.innerHTML = ''
        this.content = ''

        //消息框
        this.$notify.success({
          message: '评论成功'
        })
      } catch (err) {
        // this.$notify.error({
        //   message: '评论失败'
        // })
      }
    }
  }
}
</script>

<style lang="stylus">
.blog-comment--edit
  display grid
  grid-template-columns 1fr 1fr
  grid-template-rows 20px auto 40px
  grid-row-gap 8px
  padding 14px 10px
  .el-button
    padding 0
    line-height 40px
    grid-column 1 / 3

.blog-edit--editor
  overflow-y auto
  grid-column 1 / 3
  min-height 100px
  max-height 300px
  padding: padding-per * 2
  border 1px solid #ebebeb
  border-radius bd-radius-per
  word-break break-word
  transition 0.3s
  &:focus
    box-shadow 0 0 3px theme-color-orange inset
</style>