<template>
  <el-card class="blog-chat">
    <h2 class="blog-card--title">聊天室</h2>
    <div class="blog-chat--wrap markdown-body">
      <section
        v-for="chat in chatList"
        :key="chat.time"
        class="blog-chat--item"
        :class="chat.pos"
      >
        <p v-if="chat.nickname" class="chat-item--nickname">
          {{ chat.nickname }}
        </p>
        <div class="chat-item--content">
          <p class="chat-item--date">{{ chat.date }}</p>
          <p class="chat-item--msg">{{ chat.msg }}</p>
        </div>
      </section>
      <el-dialog
        title="亲爱的游客,欢迎你来到聊天室"
        width="500px"
        :visible="dialogVisible"
        :before-close="closeDialog"
        class="blog-chat--dialog"
      >
        <el-input v-model="nickname" placeholder="输入昵称以继续"></el-input>
        <el-button @click="guestEnter">确定</el-button>
      </el-dialog>
    </div>

    <el-input
      :disabled="!nickname"
      v-model="chatMsg"
      @keydown.native.enter="sendMsg"
      placeholder="请输入内容"
    >
      <el-button :disabled="!nickname" slot="append" @click="sendMsg"
        >发送</el-button
      >
    </el-input>
  </el-card>
</template>

<script>
import { io } from 'socket.io-client'
import formatDate from '@/utils/formatDate'
export default {
  name: 'VueBlogChat',

  data() {
    return {
      ws: null,
      //登录状态
      uid: null,
      nickname: null,
      //游客弹窗
      dialogVisible: false,
      //聊天
      chatMsg: '',
      //消息列表
      chatList: []
    }
  },
  created() {
    //验证是否为游客
    this.checkNickname()
    this.dialogVisible = !this.uid

    //如果是游客 建立新的连接 如果是登录用户直接进入聊天室
    if (this.$ws && this.$ws.connected) {
      this.ws = this.$ws
      this.ws.emit('enterChat', { uid: this.uid, nickname: this.nickname })
    } else {
      this.resetIo()
    }

    //登录成功事件
    this.loggedHandle = (nickname) => {
      this.serverLog({ nickname, isLogin: true })
      this.closeDialog()
    }

    //连接失败(昵称重复)
    this.userDupHandle = () => {
      this.$notify.warning({ message: '您输入的昵称已存在' })
      this.dialogVisible = true
    }

    //接受消息广播
    this.broadcastHandle = (data) => {
      this.serverBroadcast(data)
    }

    //其他用户离开
    this.logoutHandle = (nickname) => {
      this.serverLog({ nickname, isLogin: false })
    }
    this.ws.on('logged', this.loggedHandle)
    this.ws.on('userDup', this.userDupHandle)
    this.ws.on('broadcastMsg', this.broadcastHandle)
    this.ws.on('logout', this.logoutHandle)
  },

  methods: {
    resetIo() {
      //建立socket连接
      //if (this.ws) this.ws.close()
      this.ws = io(process.env.VUE_APP_CHAT_SOCKET_URL, {
        //重连次数最大5
        reconnectionAttempts: 5,
        //transport强制浏览器使用webSocket通信
        transports: ['websocket']
      })
    },
    //////登录前action

    //检查是否为已登录用户
    checkNickname() {
      let userInfo = this.$store.getters.userInfo
      this.nickname = userInfo?.nickname || undefined
      this.uid = userInfo?._id || undefined
    },
    showDialog() {
      this.dialogVisible = true
    },
    closeDialog() {
      this.dialogVisible = false
      //如果没有昵称返回主页
      if (!this.nickname) {
        this.$router.push('/index')
      }
    },
    /////游客登录到socket 服务器验证提交的昵称 如已存在 会重新弹框让用户输入
    guestEnter() {
      console.log('ws', this.ws, 'nickname', this.nickname)
      this.nickname && this.ws.emit('enterChat', { nickname: this.nickname })
    },

    //////登录后action
    serverLog({ nickname, isLogin }) {
      const stat = isLogin ? '进入' : '离开'
      const msg = nickname + stat + '了聊天室'
      this.addChat({ type: 'notify', msg })
    },

    //添加消息 参数
    // type [通知notify,others从他人,me自己]
    // msg 消息内容
    // nickname 昵称
    // date 消息时间
    addChat({ type, nickname, msg = '', date = formatDate() }) {
      //消息展示的位置
      let pos = {
        notify: 'center',
        me: 'right',
        others: 'left'
      }
      this.chatList.push({
        type,
        pos: pos?.[type],
        nickname,
        msg,
        date
      })
    },

    //广播消息处理(其他人的消息)
    serverBroadcast(data) {
      this.addChat({
        type: 'others',
        ...data
      })
    },
    //发送消息
    sendMsg() {
      let msg = this.chatMsg
      if (!msg.trim().length) {
        return this.$notify.warning({ message: '哎呀~还未输入聊天内容呢' })
      }
      this.ws.emit('sendMsg', msg)
      this.addChat({
        type: 'me',
        nickname: this.nickname,
        msg
      })
      this.chatMsg = ''
    }
  },

  //离开页面退出聊天室(用户不断开 游客断开连接)
  beforeDestroy() {
    this.ws.emit('leaveChat')
    this.ws.off('logged', this.loggedHandle)
    this.ws.off('userDup', this.userDupHandle)
    this.ws.off('broadcastMsg', this.broadcastHandle)
    this.ws.off('logout', this.logoutHandle)
  }
}
</script>

<style lang="stylus">
.blog-chat--wrap
  overflow-y auto
  width 100%
  height 70vh
  border 1px solid #ccc
  border-radius 6px
  padding 6px
  background-color #fcfcfc

.blog-chat--item
  width 100%
  display flex
  &.center
    justify-content center
    align-items end
  &.left
    justify-content flex-start
  &.right
    justify-content flex-start
    flex-direction row-reverse

.blog-chat--item.right, .blog-chat--item.left
  .chat-item--content
    display flex
    margin-top 25px
    padding 10px
    border-radius 8px
    flex-direction column
    color #fff
    background-color #fff
    width 30%
    box-shadow 0 0 2px #ddd

.blog-chat--item.left .chat-item--content
  background-color theme-color-green

.blog-chat--item.right .chat-item--content
  background-color theme-color-blue

.blog-chat--dialog .el-button
  margin-left calc(100% - 70px)
  margin-top 5px
</style>