import Vue from 'vue'
import http from '@/api/http'

const httpPlugin = function (Vue) {
  if (httpPlugin.installed) return
  httpPlugin.installed = true
  Object.defineProperties(Vue.prototype, {
    $http: {
      get() {
        return http
      },
      enumerable: false, // 不可枚举
      configurable: false // 不可重写
    }
  })
}
Vue.use(httpPlugin)
