import Vue from 'vue'
import Vuex from 'vuex'

// modules
import modal from '@/store/modules/modal'
import artLikes from '@/store/modules/artLikes'
//http
import http from '@/api/http'
import HTTP_CONFIG from '@/configs/http.config'
const { TOKEN_NAME } = HTTP_CONFIG
import store from 'store'
//router
import router from '@/router/index'
//webSocket
import { io } from 'socket.io-client'
//动作是否用户主动执行(用于登出)
let actByUser = false

import isMobile from '@/utils/isMobile'

Vue.use(Vuex)


export default new Vuex.Store({
  state: {
    //刷新界面的时候要获取localstorage
    token: store.get(TOKEN_NAME) || '',
    userInfo: {},
  },
  getters: {
    userInfo(state) {
      if (state.token) {
        return state.userInfo
      }
    }
  },
  mutations: {
    SET_TOKEN(state, token) {
      state.token = token
    },
    SET_USERINFO(state, userInfo) {
      state.userInfo = userInfo
    },
    LOGOUT(state) {
      store.remove(TOKEN_NAME)
      state.token = ''
      state.userInfo = {}
    }
  },
  actions: {
    //PC端跳转回首页
    PCtoIndex() {
      if (router.app.$route.path !== '/index' && !isMobile()) {
        router.push('/index')
      }
    },

    setToken({ commit }) {
      commit('SET_TOKEN', store.get(TOKEN_NAME))
    },

    //(由路由响应拦截器触发)登录后设置token 获取用户信息
    async login({ dispatch, state }) {
      dispatch('setToken')
      await dispatch('setUserInfo')
      Vue.prototype.$notify.success({
        title: '登录成功',
        message: `欢迎您, ${state.userInfo?.nickname}`,
        offset: 80
      })
      //PC端连接ws聊天室
      !isMobile() && dispatch('socketOnline')
    },

    logout({ commit, dispatch }) {
      actByUser = true
      Vue.prototype.$ws?.close()
      commit('LOGOUT')
      //PC端跳转回首页
      dispatch('PCtoIndex')
    },

    async setUserInfo({ commit, dispatch }) {
      try {
        let userInfo = await http({ type: 'getUserInfo' })
        commit('SET_USERINFO', userInfo)
        //PC端跳转回首页
        dispatch('PCtoIndex')

      } catch (error) {
        console.log(error)
      }
    },

    //建立 socket 连接
    async socketOnline({ commit, state, dispatch }) {
      let { _id: uid, nickname } = state.userInfo

      Vue.prototype.$ws = io(process.env.VUE_APP_CHAT_SOCKET_URL, {
        //重连次数最大5
        reconnectionAttempts: 5,
        //transport强制浏览器使用webSocket通信
        transports: ['websocket']
      })

      Vue.prototype.$ws.emit('online', { uid, nickname })

      //单点登录(包括用户主动登出)
      Vue.prototype.$ws.on('disconnect', () => {
        let message = actByUser ? `再见,${nickname}` : '账号已在其他设备登录'
        Vue.prototype.$notify.success({
          title: '您已登出',
          message,
          offset: 80
        })
        commit('LOGOUT')
        //PC端跳转回首页
        dispatch('PCtoIndex')
        actByUser = false
        Vue.prototype.$ws = null
      })
    }
  },
  modules: {
    modal, artLikes
  },
})
