export default {
  namespaced: true,
  state: () => ({
    modalVisible: false,
    modalType: '',
  }),
  mutations: {
    OPEN(state) {
      state.modalVisible = true;
    },
    CLOSE(state) {
      state.modalVisible = false;
    },
    SWITCH(state, type) {
      state.modalType = type;
    },
  },
  actions: {
    openModal({ commit, dispatch }, type) {
      dispatch('switchModal', type)
      commit('OPEN')
    },
    closeModal({ commit }) {
      commit('CLOSE')
    },
    switchModal({ commit }, type) {
      commit('SWITCH', type)
    },

  },

}