import store from 'store'
import HTTP_CONFIG from '@/configs/http.config'
const { ART_LIKES_NAME } = HTTP_CONFIG
import Vue from 'vue'

export default {
  namespaced: true,
  state: () => ({
    //本地存储当前用户点赞过的文章aid数组
    storLikes: store.get(ART_LIKES_NAME) || [],
    //step_num 临时点赞计数器
    step_num: 0
  }),
  getters: {
    //用户点赞的文章是否位于列表
    isLike: state => aid => !!state.storLikes.includes(aid),
  },
  mutations: {
    //主动从本地存储拿取列表
    UPDATE_LIKES(state) {
      state.storLikes = store.get(ART_LIKES_NAME);
    },
    CHANGE_STEP_NUM(state, num) {
      state.step_num = num
    }
  },
  actions: {
    changeStepNum({ commit }, num) {
      commit('CHANGE_STEP_NUM', num)
    },
    //changeLike 根据payload 决定行为
    // payload { aid: 文章id action: push | pull , initLike: 已点赞 true/false }
    changeLike({ state, commit, dispatch }, payload) {
      const { aid, action, initLike } = payload
      if (aid) {
        const likes = state.storLikes

        if (action === 'push') {
          likes.push(aid)
          //如果初始进入已经是点赞的 push动作不能+1 而是+0
          let change = initLike ? 0 : 1
          dispatch('changeStepNum', change)
        }

        if (action === 'pull') {
          let idx = likes.indexOf(aid)
          if (idx !== -1) {
            likes.splice(idx, 1)
            dispatch('changeStepNum', -1)
          }
        }

        //修改本地存储(使用Set去重)
        store.set(ART_LIKES_NAME, [...new Set(likes)])
        //更新vuex
        commit('UPDATE_LIKES')
      }
    },
    //发送服务器请求
    async sendLike(context, payload) {
      const { aid } = payload
      try {
        await Vue.prototype.$http({
          type: 'artLikes',
          data: { id: aid }
        })
      } catch (err) {
        //
      }
    }
  },

}