import JSEncrypt from 'jsencrypt'
import store from 'store'
import instance from '@/api/httpBase'
import API_LIST from '@/configs/api.config'
import HTTP_CONFIG from '@/configs/http.config'


//通过公钥加密 value
export default async function encrypt(value) {
  const pubKeyName = HTTP_CONFIG.PUBKEY_NAME
  //先从本地storage 尝试获取公钥
  let pubKey = store.get(pubKeyName)
  try {
    if (!pubKey || pubKey === 'undefined') {
      //如果没有 重新请求PubKey
      let { data } = await instance.get(API_LIST['pubKey']['path'])
      //获取过来的key存在换行符比如 '\n' 需要替换掉
      pubKey = data.pubKey
      pubKey = pubKey.replace(/\. +/g, '')
      pubKey = pubKey.replace(/[\r\n]/g, '')
      //调用 store 存储公钥到 local storage
      store.set(pubKeyName, pubKey)
    }

    let encrypt = new JSEncrypt()
    encrypt.setPublicKey(pubKey)
    return encrypt.encrypt(value)
  } catch (error) {
    return error
  }
}
