import Vue from 'vue'
import VueRouter from 'vue-router'

//Desktop view
//import Home from '@/views/Home.vue'
//import ArticleList from '@/views/ArticleList.vue'
import Article from '@/views/Article.vue'
//import Column from '@/views/Column.vue'
//import Editor from '@/views/Editor.vue'
import About from '@/views/About.vue'
import Chat from '@/views/Chat.vue'
//import User from '@/views/User.vue'

//路由按需加载 (相同的模块可以起同一名称)
const Home = () => import(/* webpackChunkName: 'home~article_list' */ '@/views/Home.vue');
const ArticleList = () => import(/* webpackChunkName: 'home~article_list' */ '@/views/ArticleList.vue');
const Column = () => import(/* webpackChunkName: 'column' */ /* webpackMode: "lazy" */ '@/views/Column.vue');
const Editor = () => import(/* webpackChunkName: 'editor' */ /* webpackMode: "lazy" */ '@/views/Editor.vue');
const User = () => import(/* webpackChunkName: 'editor' */  /* webpackMode: "lazy" */'@/views/User.vue');


//Mobile view
//import mHome from '@/views/mobile/Home.vue'
//import mArticleList from '@/views/mobile/ArticleList.vue'

const mArticle = () => import(/* webpackChunkName: 'm-common' */'@/views/mobile/Article.vue')
const mColumn = () => import(/* webpackChunkName: 'm-common' */'@/views/mobile/Column.vue')
const mUser = () => import(/* webpackChunkName: 'm-common' */'@/views/mobile/User.vue')
const mHome = () => import(/* webpackChunkName: 'm-home~article_list' */ '@/views/mobile/Home.vue');
const mArticleList = () => import(/* webpackChunkName: 'm-home~article_list' */ '@/views/mobile/ArticleList.vue');


//NProgress 进度条 修改进度条样式 自行hackcss
import NProgress from 'nprogress'
import "nprogress/nprogress.css";

import isMobile from '@/utils/isMobile'

//解决路由跳转返回同一地址报错
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
const originalReplace = VueRouter.prototype.replace
VueRouter.prototype.replace = function replace(location) {
  return originalReplace.call(this, location).catch(err => err)
}
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home,
    redirect: '/index',
    children: [
      {
        path: '/index',
        name: 'articleList',
        component: ArticleList,
        props: (route) => {
          return route.query
        }
      },
      {
        path: '/article/:id',
        name: 'article',
        component: Article,
      },
      {
        path: '/column',
        meta: { authRequired: true },
        name: 'column',
        component: Column,
      },
      {
        path: '/editor',
        meta: { authRequired: true },
        name: 'editor',
        component: Editor,
        props: (route) => {
          return route.query
        }
      },
      {
        path: '/user',
        name: 'user',
        component: User,
      },
      {
        path: '/chat',
        name: 'chat',
        component: Chat,
      },
      {
        path: '/about',
        name: 'about',
        component: About,
      },
    ]
  },
  {
    path: '/m',
    name: 'mHome',
    redirect: '/m/articles',
    component: mHome,
    children: [
      {
        path: 'articles',
        name: 'mArticleList',
        component: mArticleList,
        meta: {
          headCfg: {
            headType: 'SearchBar',
          }
        },
        props: (route) => {
          return route.query
        }
      },
      {
        path: 'article/:id',
        name: 'mArticle',
        component: mArticle,
        props: true,
        meta: {
          headCfg: {
            headType: 'NavBar',
            title: '文章正文',
            button: '分享',
            icon: 'share-o',
            btnHandler: 'show-art-share',
          }
        },
      },
      {
        path: 'column',
        //meta: { authRequired: true },
        name: 'mColumn',
        component: mColumn,
        meta: {
          headCfg: {
            headType: 'NavBar',
            title: '分类',
            button: '',
            btnHandler: '',
          }
        },
      },
      {
        path: 'user',
        name: 'mUser',
        component: mUser,
        meta: {
          headCfg: {
            headType: 'NavBar',
            title: '我的',
            button: '注册',
            btnHandler: 'switch-user-header',
          }
        },
        props: ({ query }) => ({
          state: query?.state,
        }),
        //用户界面右上角切换 登录/注册 按钮 以及登录dialog传送
      },
    ]
  }
]

const router = new VueRouter({
    // mode: 'history', // 如果需要路由改为 history
  routes
})

router.beforeEach((to, from, next) => {
  //移动端不使用导航条
  if (isMobile()) {
    next()
    return
  }
  NProgress.start()
  //需要认证的路径 并且没有token
  if (to.meta?.authRequired && !router.app.$store.state.token) {
    //从store获取token
    Vue.prototype.$notify.warning({
      title: '警告',
      offset: 100,
      message: '很抱歉, 您没有访问权限',
    })
    NProgress.done()
    next('/index')
  }
  next()
})

router.afterEach(() => {
  if (!isMobile()) {
    NProgress.done()
  }
})
export default router
