<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
import isMobile from '@/utils/isMobile'
export default {
  name: 'VueBlogApp',
  data() {
    return {}
  },
  mounted() {
    this.switchMobileView()
  },

  methods: {
    switchMobileView() {
      let route = !!isMobile() ? '/m' : '/'
      this.$router.push(route)
    }
  }
}
</script>

<style lang="stylus">
#app
  overflow hidden
</style>
